//Î∞òÎ≥µÎ¨∏ - Î™ÖÎ†πÏùÑ ÏùºÏ†ï ÌöüÏàòÎßåÌÅº Î∞òÎ≥µÌï¥ ÏàòÌñâÌïòÎèÑÎ°ù Ï†úÏñ¥
//while, do-while, for, for-in, for-ofÎ¨∏
//ÌëúÌòÑÏãùÏùò Í≤ÄÏÇ¨Î•º ÌÜµÌï¥ Î∞òÎ≥µÌï¥ÏÑú Ïã§ÌñâÎêòÎäî Î∞òÎ≥µÎ¨∏ - Î£®ÌîÑ(loop)

//1.whileÎ¨∏ - ÌäπÏ†ï Ï°∞Í±¥ ÎßåÏ°±Ìï† ÎïåÍπåÏßÄ Í≥ÑÏÜçÌï¥ Î∞òÎ≥µ
var i = 1;
