//ÌîÑÎ°úÍ∑∏Îû®ÏùÄ Î£®ÌîÑÎ•º Í≥ÑÏÜç Î∞òÎ≥µÌïúÎã§. - Î¨¥ÌïúÎ£®ÌîÑ
//*Ï¶â Ïñ¥ÎäêÏàúÍ∞ÑÏóêÎäî Í±∞ÏßìÏùò Í≤∞Í≥ºÎ•º Í∞ñÎèÑÎ°ù Ìï¥ÏïºÌï®

//2.do-while
//do-whileÎ¨∏ÏùÄ Î®ºÏ†Ä Î£®ÌîÑÎ•º 1Ìöå Ïã§Ìñâ ÌõÑ ÌëúÌòÑÏãùÏùÑ Í≤ÄÏÇ¨ÌïúÎã§.

