arr = new Set([1, 1, 2, 2, 3, 3]);
//set Í∞ùÏ≤¥-ÎèôÏùº Í∞í Ï§ëÎ≥µ Ìè¨Ìï®x, ÏàúÏÑú ÏùòÎØ∏ x, Ïù∏Îç±Ïä§Î°ú Ï†ëÍ∑º x

